'use client'
import { motion } from "framer-motion";
import React from "react";
import { ImagesSlider } from "./ui/images-slider";
import { TextGenerateEffect } from "./ui/text-generate-effect";

export function ImagesSliderDemo() {
  const images = [
    "https://images.unsplash.com/photo-1485433592409-9018e83a1f0d?q=80&w=1814&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D",
    "https://images.unsplash.com/photo-1483982258113-b72862e6cff6?q=80&w=3456&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D",
    "https://images.unsplash.com/photo-1482189349482-3defd547e0e9?q=80&w=2848&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D",
  ];

  return (
    <ImagesSlider className="h-[40rem]" images={images}>
      <motion.div
        initial={{
          opacity: 0,
          y: -80,
        }}
        animate={{
          opacity: 1,
          y: 0,
        }}
        transition={{
          duration: 0.6,
        }}
        className="absolute inset-0 z-50 flex flex-col justify-center items-center text-center p-6 md:p-12"
      >
        {/* Main Donation Message */}
        <motion.h1 className="font-bold text-3xl mt-11 md:text-5xl text-white bg-clip-text text-transparent bg-gradient-to-r from-green-700 to-white mb-4 shadow-lg
        ">
          Make a Difference, One Donation at a Time

          {/* Make a Difference, <span className= "font-bold text-3xl mt-11 md:text-5xl text-transparent bg-clip-text bg-gradient-to-r from-white to-[#457108] mb-4">
          One Donation at a Time</span> */}
        </motion.h1>

        <motion.p className="font-semibold text-lg md:text-2xl text-white max-w-2xl mb-6">
          <TextGenerateEffect
            words="“The best charity is that which is given in secret, so that the left hand does not know what the right hand has given.”"
            className="italic text-white text-center"
            filter={false}
            duration={0.9}
          />
          <br />
          <span className="font-bold">— Prophet Muhammad (PBUH)</span>
        </motion.p>

        {/* Call to Action Text */}
        <motion.p className="font-light text-lg md:text-xl text-white mb-8 max-w-lg">
          Your contribution can help transform lives. Whether it’s for <strong>zakat</strong>, fitra, or sadqa, every donation matters. Help us support those in need today!
        </motion.p>

        {/* Donate Button */}
        <motion.button className="px-6 py-3 backdrop-blur-sm border bg-emerald-300/10 border-emerald-500/20 text-white mx-auto text-center rounded-full relative mt-4 hover:bg-emerald-500 transition duration-300">
          <span className="font-semibold">Donate Now →</span>
          <div className="absolute inset-x-0 h-px -bottom-px bg-gradient-to-r w-3/4 mx-auto from-transparent via-emerald-500 to-transparent" />
        </motion.button>

        {/* Secondary Inspirational Text */}
        <motion.p className="text-white text-xs md:text-sm mt-6 max-w-lg">
          “Even the smallest act of <strong>kindness</strong> can change someone's life. Together, we can create a better tomorrow.”
        </motion.p>
      </motion.div>
    </ImagesSlider>
  );
}
